.nav {
  padding: 0 12vw;
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;

  &__togler-label {
    display: inline-block;
    padding: 20px;
    color: wheat;
    font-size: 4rem;
  }

  &__list {
    display: flex;
    justify-content: space-between;

    // @media screen and (max-width: 640px) {
    //   display: none;
    // }
  }

  &__item {

    &-active {
      position: relative;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 8px;
    }
  }

  &__link {
    padding: 8px 24px;
    text-decoration: none;
    font-size: 12px;
    color: #fff;
    text-transform: uppercase;
    position: relative;
    white-space: nowrap;

    @include hover(color, $activeColor);

    @media screen and (max-width: 900px) {
      padding: 8px 14px;
    }
  }
}

.nav__togler {
  display: none;

  // @media screen and (max-width: 640px) {
  // display: block;
  // }
}

@media screen and (max-width: 640px) {
  .nav__list {
    display: none;
  }

  .nav__togler:checked + .nav__list {
    display: flex;
  }

  .menuToggle {
    display: block;
    position: absolute;
    bottom: 439px;
    left: 78vw;
    z-index: 1;

    &__hamburger {
      display: block;
      width: 18px;
      height: 2px;
      margin-bottom: 2px;
      position: relative;

      background: #fff;
      border-radius: 2px;
      z-index: 1;

      transform-origin: 4px 0;

      transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
        background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
        opacity 0.55s ease;

      &:first-child {
        transform-origin: 0% 0%;
      }

      &:nth-last-child(2) {
        transform-origin: 0% 100%;
      }
    }

    &__chek {

      &:checked ~ .menuToggle__hamburger {
        opacity: 1;
        transform: rotate(45deg) translate(-2px, -1px);
        background: #fff;
      }

      &:checked ~ .menuToggle__hamburger:nth-last-child(3) {
        opacity: 0;
        transform: rotate(0deg) scale(0.2, 0.2);
      }

      &:checked ~ .menuToggle__hamburger:nth-last-child(2) {
        transform: rotate(-45deg) translate(0, -1px);
      }

      &:checked ~ ul {
        transform: none;
      }
    }
  }
}
